
import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default {
    name: "router",
    created() {
        this.$router.options.routes.forEach(route => {
            if (route.meta.showInRoutes) {
                this.availableRoutes.push({
                    name: route.name,
                    path: route.path,
                    showInRoutes: route.meta.showInRoutes,
                    title: route.meta.title
                })
            }
        });
    },
    data() {
        return {
            availableRoutes: []
        }
    }
}